export default {
	data() {
		return {
			arr:[],
		};
	},
	computed: {
		
	},
	created() {
		this.capital();
	},
	mounted() {},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return MM ;
		},
		formatDates: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y ;
		}
	},
	methods: {
		phone(){
			uni.makePhoneCall({
				phoneNumber: '4006182288' //拨打电话
			});
		},
		capital(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/proj/info/51?lookUserId='+landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.arr=response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			} else if (uni.getStorageSync('UUID')) {
				let UUID = uni.getStorageSync('UUID'); // 读取缓存的用户信息
				console.log(UUID);
				 // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/proj/info/51?lookUserId='+ UUID, //接口地址。
					// data: params,
					method: 'GET',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.arr=response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		}
	}
};
